import React from 'react';
import {inject, observer} from 'mobx-react';
import DeliveryStore from "@/store/delivery/DeliveryStore";
import {withRouter} from "react-router-dom";
import url from 'url';
import { Button, Input, message, Modal, Empty } from 'antd';
import Zmage from 'react-zmage';

@inject('DeliveryStore')
@withRouter
@observer
export default class Document extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
		    id: "",
            visible: false,
            item: {},
            value: "",
            dataSource: [
                {
                    id: 1,
                    taskId: "1",
                    name: "场景1.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                },
                {
                    id: 2,
                    taskId: "1",
                    name: "场景2.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                },
                {
                    id: 3,
                    taskId: "1",
                    name: "场景3.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                },

                {
                    id: 4,
                    taskId: "1",
                    name: "场景4.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                },
                {
                    id: 5,
                    taskId: "1",
                    name: "场景5.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                },
                {
                    id: 6,
                    taskId: "1",
                    name: "场景6.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                }, {
                    id: 7,
                    taskId: "1",
                    name: "场景7.jpg",
                    ownerId: "",
                    ownerName: "",
                    fileSize: "",
                    createTime: "",
                    orders: "",
                    isVisible: "",
                    downloadCount: "",
                    fileUrl: "https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11/section1-1.jpg",
                    fileType: "",
                    category: 2
                }
            ]
        }
	}

	componentDidMount() {
        let search = this.props.location.search;
        const id = url.parse(search, true).query.taskId;
        this.setState({
            id
        }, ()=>{
            this.getDocumentListByCategory();
        });
    }

    getDocumentListByCategory = () => {
	    const params = {
            taskId: this.state.id,
            category: 2
        }
        this.store.getDocumentListByCategory(params)
            .then((res)=>{
                this.setState({
                    dataSource: res
                });
            });
    }

    showModal = (item) => {
        //获取后缀
        function getExt(filePath) {
            // 去掉url的问号
            if (filePath.indexOf("?") !=-1) {
                filePath = filePath.split("?")[0];
            }
            //获取最后一个.的位置
            let index = filePath.lastIndexOf(".");
            //获取后缀
            let ext = filePath.substr(index + 1);
            let name = filePath.substr(0, index);
            return {name, ext};
        }
        this.setState({
            visible: true,
            item,
            value: item.name, //getExt(item.name).name,
            //ext: getExt(item.name).ext,
        });
    };

    handleOk = () => {
        let {item, value, ext} = this.state;
        if (value==null||value=="") {
            message.warn("名称不能为空");
            return false;
        }
        let obj = JSON.parse(JSON.stringify(item));
        obj.name = value;
        this.store.updateDocument({d: obj}).then((res)=>{
          if (res) {
              message.success("操作成功");
              this.getDocumentListByCategory();
              this.setState({
                  visible: false,
              });
          }
        });
    };

    handleCancel = () => {
        this.setState({
            visible: false,
        });
    };

    handleChange = (e) => {
       this.setState({
          value: e.target.value
       });
    }

	render() {
	    const {dataSource, visible, value} = this.state;
		return (
			<div style={{
			    width: "1200px",
                minHeight: "100vh",
                background: "#F5F5F5",
                margin: "0 auto"
            }}>
              <h1 style={{padding: "15px"}}>场景照片</h1>
                <ul className="brick-list">
                    {dataSource.map((item)=>{
                        return <li key={item.id}
                                   className="brick-item"
                                   >
                            <Zmage
                                width="186px"
                                height="160px"
                                src={item.fileUrl}
                            />
                            <p>{item.name}<Button
                                onClick={this.showModal.bind(this, item)}
                                style={{fontSize: "12px"}}
                                type="link">重命名</Button></p>
                        </li>
                    })}
                </ul>
                {dataSource.length==0?<Empty />:null}
                <Modal
                    title="重命名"
                    visible={visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Input
                        value={value}
                        onChange={this.handleChange}
                        placeholder="请输入" />
                </Modal>
			</div>
		)
	}
}






